@-webkit-keyframes slide {
    0% {
        object-position: 0% 0%;
    }
    5% {
        object-position: 0% 0%;
    }
    50% {
        object-position: 100% 0%;
    }
    55% {
        object-position: 100% 0%;
    }
    100% {
        object-position: 0% 0%;
    }
}

