}

/* General styles */

.news-card {
    display: flex;
    display: -webkit-flex;
    flex: 0 0 auto;
    
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-content: stretch;
}

.news-card-tag {
    display: block;
    text-transform: uppercase;
    font-weight: 300;
    font-size: 18px;
    line-height: 140%;

    color: #dbdbdb;
    margin: 12px 0;
}

.news-card-text a:hover {
    text-decoration: underline;
}

.news-card img {
    object-fit: cover;
    font-weight: 300;
}

.news-card h2 {
    font-style: normal;
    font-weight: 300;
    font-size: 36px;
    line-height: 110%;
}

.news-card h2:hover {
    text-decoration: underline;
}

.news-card-excerpt {
    font-style: italic;
    font-weight: 300;
    font-size: 18px;
    line-height: 140%;

    color: #b3b3b3;

    margin-top: 8px;
}

/* 'Main' news cards */

.news-card.main {
    flex-direction: column;
    justify-content: flex-start;
    align-content: center;
    align-items: flex-start;
    height: auto;
    width: 100%;

    margin-bottom: 20px;
}

.news-card.main .news-card-image {
    width: 100%;
}

.news-card.main img {
    width: 100%;
    height: auto
}

/* 'Fresh' news cards */

.news-card.fresh {
    width: 100%;
}

.news-card.fresh {
    flex-direction: row;
    height: auto;
    width: 100%;

    margin-bottom: 20px;
}

.news-card.fresh img {
    height: 6vw;
