    width: 100%;
    height: auto;
    box-sizing: border-box;
    overflow: visible;
}

.about::-webkit-scrollbar {
    width: 2px;
}

.about::-webkit-scrollbar-track {
    background-color: #00000000;
    border: 0;
}

.about::-webkit-scrollbar-thumb {
    background-color: #ffffff13;
}

.about::-webkit-scrollbar-button {
    display: none;
}

.about .information, .about .image {
    width: 50%;
}

.about.mobile .information,
.about.mobile .image {
    width: 100%;
}

.about .information {
    display: flex;
    display: -webkit-flex;
    flex: 0 0 auto;
    
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-content: center;
    align-items: flex-start;

    margin-bottom: 101px;
}

.about.mobile .information {
    margin: 0;
}

.about .information h1 {
    font-style: normal;
    font-weight: 300;
    font-size: 68px;
    line-height: 120%;
    color: #ffffff;
}

.about.mobile .information h1 {
    margin-top: 20px;
}

.about .information p {
    font-style: normal;
    font-weight: 300;
    font-size: 18px;
    line-height: 180%;

    margin: 40px 0 60px;
}

.about.mobile .information p {
    margin: 20px 0 30px;
}

.about.mobile .information .social-links {
    margin-bottom: 30px;
}

.about .image {
    display: flex;
    display: -webkit-flex;
    flex: 0 0 auto;
    
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-content: center;
    align-items: center;
}

.about .image img {
    width: 80%;
    height: auto;
}

.about.mobile .image img {
    width: 100%;
}

.android-link {
    color: white;
    color: #ffffff;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    display: flex;
    display: -webkit-flex;
    flex: 0 0 auto;
    
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-content: stretch;
    align-items: center;
}

.about-android-link-wrapper {
    position: relative;
    margin-bottom: 20px;
}

.about-android-link-wrapper .android-link:before {
    content: '';
    position: absolute;
    width: 1%;
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: #ffffff;
    visibility: hidden;
}

.about-android-link-wrapper .android-link:hover:before {
    visibility: visible;
    width: 100%;
}

.about-android-link-wrapper .google-play-icon {
    width: 1.1em;  
    margin: 0 12px;
}


