.media-container {
    width: 100%;
    height: 320px;

    margin-bottom: 20px;

    overflow: hidden;
}

.media-image {
    position: relative;
    top: 0;
    left: 0;

    z-index: 2;

    width: 100%;
    height: 320px;

    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;

    transition: height 0ms 400ms, opacity 400ms 0ms;
}

.media-image:hover .mask {
    display: flex;
}

.media-image .mask {
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: center;
    align-content: center;
    align-items: center;

    width: 100%;
    height: 100%;

    background-color: #00000044;

    display: none;
}

.media-image.hidden {
    height: 0;
    opacity: 0;
}

.media-image.visible {
    height: 320px;
    opacity: 1;
    transition: height 0ms 0ms, opacity 600ms 0ms;
}

.media-video {
    width: 100%;
    height: 100%;
}

