html,
body {
    height: 100vh;
}

body {
    margin: 0;
    background: #000000;
    font-family: "Poppins", "Manrope", BlinkMacSystemFont, "Helvetica Neue";
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: black;

    overflow: hidden;
}

@media (pointer: none), (pointer: coarse) {
    html,
    body {
        height: auto;
    }

    body {
        overflow: visible;
    }
}

h1,
